* {
  padding: 0;
  margin: 0;
}

body {
  font-family: 'Comic Sans MS', cursive, sans-serif;
  background: linear-gradient(135deg, #ff9a9e 0%, #fecfef 100%);
  color: #ff4d4d;
  text-align: center;
  margin: 0;
  padding: 0;
  animation: backgroundPulse 5s infinite alternate;
}

.container {
  box-sizing: border-box;
  padding-top: 100px;
  padding-bottom: 100px;
  width: 100vw;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  animation: fadeIn 2s ease-in-out;
}

img {
  border-radius: 20px;
  width: 500px;
  height: 300px;
  margin-bottom: 70px;
  animation: float 6s infinite alternate;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

h1 {
  font-size: 50px;
  text-transform: capitalize;
  animation: bounce 3s infinite;
  color: #ff4d4d;
  text-shadow: 2px 2px #ffe6e6;
}

.buttons {
  margin-top: 70px;
  display: flex;
  gap: 20px;
  text-align: center;
}

button {
  box-sizing: border-box;
  height: 40px;
  width: 80px;
  border-radius: 20px;
  padding: 10px;
  gap: 10px;
  border: none;
  font-size: 20px;
  transition: ease 0.3s;
  font-weight: 600;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

button:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.Yes {
  background-color: rgb(36, 235, 36);
}

.No {
  background-color: rgb(252, 45, 45);
}

.Restart {
  margin-top: 20px;
  background-color: #ff4d4d;
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
  transition: transform 0.2s;
}

.Restart:hover {
  background-color: #ff6666;
  transform: scale(1.1);
}

.hearts {
  margin-top: 20px;
  display: flex;
  gap: 10px;
  animation: heartBeat 1s infinite;
}

.heart {
  font-size: 30px;
  animation: heartPulse 1s infinite alternate;
}

.quote {
  margin-top: 20px;
  font-size: 24px;
  color: #ff4d4d;
  animation: fadeIn 2s ease-in-out;
}

@keyframes backgroundPulse {
  from {
    background-color: #ffe6e6;
  }
  to {
    background-color: #ffcccc;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes float {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-20px);
  }
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}

@keyframes heartPulse {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.2);
  }
}
